/**
 * Logger-Browser
 * @module Logger-Browser
 */

const color = {
  blue: '#8fc9fb',
  green: '#64ea91',
  purple: '#d897eb',
  red: '#f69899',
  yellow: '#f8c82e',
  peach: '#f797d6',
  grass: '#d6fbb5',
  sky: '#c1e0fc'
};

const normal = [
  `background: ${color.blue}`,
  'color: black',
  'display: block',
  'text-align: center'
].join(';');

const ok = [
  `background: ${color.green}`,
  'color: black',
  'display: block',
  'text-align: center'
].join(';');

const warning = [
  `background: ${color.yellow}`,
  'color: black',
  'display: block',
  'text-align: center'
].join(';');

const failure = [
  `background: ${color.red}`,
  'color: black',
  'display: block',
  'text-align: center'
].join(';');

const ajax = [
  `background: ${color.purple}`,
  'color: black',
  'display: block',
  'text-align: center'
].join(';');

/**
 * 日志类
 * @class Logger
 */
class Logger {
  /**
   * 信息
   * @memberof Logger
   */
  public info = (msg: string, msg2?: string) => {
    console.log(`%c${msg} ${msg2 ? '%O' : ''}`, normal, msg2 || '');
  };

  public success = (msg: string, msg2?: string) => {
    console.log(`%c${msg} ${msg2 ? '%O' : ''}`, ok, msg2 || '');
  };

  public warn = (msg: string, msg2?: string) => {
    console.log(`%c${msg} ${msg2 ? '%O' : ''}`, warning, msg2 || '');
  };

  public error = (msg: string, msg2?: string) => {
    console.log(`%c${msg} ${msg2 ? '%O' : ''}`, failure, msg2 || '');
  };

  public network = (msg: string, msg2?: string) => {
    console.log(`%c${msg} ${msg2 ? '%O' : ''}`, ajax, msg2 || '');
  };
}

export default Logger;
